.container-form-dt{
    margin: 4rem 0 4rem 0;
}
.logo-form img{
    width: 30%;
    margin-bottom: 2rem;
}
.logo-form h2{
    font-weight: bold;
}

/****************HEADER******************/
.container-banner-header{
    background-color: black;
}
.foto-logo{
    display: flex;
    align-items: center;
    padding: 5rem 0 5rem 0;
}
ul li{
    list-style: none;
}
a{
    text-decoration: none;
}
.header-nav{
    margin: 0rem 0 3rem 0;
    padding-top: 1rem;
}
.nav-link .billetera{
    color: yellow;
    font-size: 32px;
    font-weight: bold;
}
.nav-link .mochila{
    color: #ff00dd;
    font-size: 32px;
    font-weight: bold;
}
.nav-link .gorra{
    color: #6906c5;
    font-size: 32px;
    font-weight: bold;
}
.nav-link .contacto{
    color: #67616d;
    font-size: 32px;
    font-weight: bold;
}

/*************CARDPRODUCT INDEX***************/
.titulo-body-tienda h2{
    color: white;
}

.product-img img{
    width: 30vh;
    height: 30vh;
}
.card-container{
    margin: 2rem 0 2rem 0;
}
.card-producto-index{
    border: 1px solid black;
    border-radius: 10px;
    background-color: #fafafa;
}
.card-title{
    height: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 90%;
    margin: auto;
}
.ellipsis-text-title{
    text-overflow: ellipsis;
}

#button-vermas{
    padding: 1rem;
    border-radius: 5px;
    background-color: #6906c5;
    color: white;
    text-decoration: none;
    font-weight: bold;
}
#precio-card{
    color: #464545;
    font-weight: bold;
}

.body-tienda{
    background: rgb(2,0,36);
    background: linear-gradient(14deg, rgba(2,0,36,1) 35%, rgba(2,29,83,0.9416141456582633) 82%);
}

.medios-pago-haiblitado img{
    width: 30%;
    text-align: center;
}

.container-pagos-medios{
    padding: 2rem 0 2rem 0;
}

/********************************************/
.container-envio-bsas{
    background: rgb(2,0,36);
    background: linear-gradient(14deg, rgba(2,0,36,1) 35%, rgba(2,29,83,0.9416141456582633) 82%);
}
.container-envio-bsas img{
    width: 20%;
}
.txt-envio-index{
    padding: 2rem 0 1rem 0;
}
.txt-envio-index p{
    color: white;
}

/**************FOOTER**********/
.footer-container{
    background-color: black;
    margin: 0;
}
.footer-container ul{
    padding: 0;
}
.footer-container ul li{
    color: white;
}
.index-logo-footer img{
    width: 60%;
}
.mercadopago-footer img{
    width: 28%;
}
.footer-container h3{
    color: white;
}
.seccion-footer ul li a{
    color: white;
}